.motel-detail-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.motel-address {
  font-style: italic;
  color: #666;
}

.motel-rating {
  font-weight: bold;
  color: #f39c12;
  margin-bottom: 20px;
}

.motel-images {
  display: flex;
  overflow-x: auto;
  gap: 10px;
  margin-bottom: 20px;
}

.motel-images img {
  width: 300px;
  height: auto;
  object-fit: cover;
}

.motel-description,
.motel-amenities,
.motel-room-types,
.motel-reviews {
  margin-bottom: 30px;
}

.motel-amenities ul,
.motel-room-types ul {
  list-style-type: none;
  padding: 0;
}

.motel-amenities li,
.motel-room-types li {
  margin-bottom: 10px;
}

.review {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 15px;
}

h2 {
  border-bottom: 2px solid #3498db;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

.motel-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.motel-info {
  flex: 1;
}

.motel-map {
  flex: 0 0 600px;
  height: 400px;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
}

