/* ìŠ¬ë¼ì´ë” ìŠ¤íƒ€ì¼ */
.motel-slider {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 30px;
  overflow: hidden;
}

.slide-item {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  cursor: pointer; /* ì´ë¯¸ì§€ í´ë¦­ ê°€ëŠ¥í•˜ë„ë¡ ì»¤ì„œ ë³€ê²½ */
}

.slide-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  display: block;
  margin: 0 auto;
}

.custom-dots-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.slick-dots li button:before {
  font-size: 10px;
  color: gray;
  opacity: 0.7;
}

.slick-dots li.slick-active button:before {
  color: black;
}

.custom-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 18px;
  z-index: 10;
}

.custom-prev {
  left: 10px;
}

.custom-next {
  right: 10px;
}

/* ë°˜ì‘í˜• ë””ìì¸ */
@media (max-width: 768px) {
  .motel-slider {
    max-width: 100%;
  }

  .custom-arrow {
    font-size: 14px;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .slide-image {
    max-width: 95%;
  }
}
